<style scoped>
  .masonry {
    display: flex;
    flex-direction: column;
  }
  @media only screen and (min-width: 500px) {
    .masonry {
      flex-direction: row;
    }
  }

  .column {
    display: flex;
    flex-flow: column wrap;
    width: 100%;
  }
  @media only screen and (min-width: 500px) {
    .column {
      width: calc(100%/5);
    }
  }

  .item {
    box-sizing: border-box;
    padding: 10px;
    counter-increment: item-counter;
  }
  .item__content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
