    background: #546e08;
  }
  .item__content--medium:after {
    content: '◔ᴗ◔';
  }
  .item__content--large {
    color: #384E77;
    height: 280px;
  }
  .item__content--large:hover {
    background: #456093;
  }
  .item__content--large:before {
    background: #c9d3e5;
  }
  .item__content--large:after {
    content: 'ಠ_๏';
  }
</style>



